.app {
  width: 100%;
  height: 95vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.heading {
  font-size: 50px;
}

.form {
  display: flex;
  flex-direction: column;
  padding: 30px;
  border: 2px solid black;
}

.input {
  font-size: 20px;
  margin: 0;
}

.str {
  margin: 15px 0;
}

.submit-button {
  width: 100%;
  height: 50px;
  background-color: #aaa;
  border: 1px solid black;
}

.submit-button:hover {
  background-color: #636363;
}

.result {
  margin: 15px 0 0 0;
  font-size: 30px;
}